<template>
  <div class="container">
    <!-- 상단 네비게이션 -->
    <nav class="navbar">
      <router-link to="/hoonlog/JH_Home" class="logo">HoonLog</router-link>
      <div class="nav-links">
        <router-link to="/about">About</router-link>
        <router-link to="/journal">Journal</router-link>
        <router-link to="/contact">Contact</router-link>
      </div>
    </nav>

    <!-- 메인 갤러리 -->
    <section class="gallery">
      <div
        v-for="(year, index) in years"
        :key="index"
        class="gallery-item"
        @click="toggleList(index)"
      >
        <img :src="year.thumbnail" :alt="year.title" />
        <div class="overlay">
          <h2>{{ year.title }}</h2>
        </div>

        <ul v-if="year.showList" class="sublist">
          <li v-for="(item, subIndex) in year.items" :key="subIndex">
            <router-link :to="item.link">{{ item.name }}</router-link>
          </li>
        </ul>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  name: 'JH_Home',
  data() {
    return {
      years: [
        { 
          title: '2019',
          thumbnail: require('@/assets/2019/2019_04.png'),
          items: [{ name: '♥2019♥', link: '/hoonlog/jhlist' }],
          showList: false 
        },
        { 
          title: '2020',
          thumbnail: require('@/assets/2020/2020_01.jpg'),
          items: [{ name: '♥2020♥', link: '/hoonlog/jhlist2' }],
          showList: false 
        },
        { 
          title: '2021',
          thumbnail: require('@/assets/2021a/2021a_19.jpg'),
          items: [
            { name: '♥2021 03♥', link: '/hoonlog/jhlist3' },
            { name: '♥2021 10♥', link: '/hoonlog/jhlist4' },
            { name: '♥2021 11♥', link: '/hoonlog/jhlist5' }
          ],
          showList: false
        },
        { 
          title: '2022',
          thumbnail: require('@/assets/2022c/2022c_6.jpg'),
          items: [
            { name: '♥2022 1분기♥', link: '/hoonlog/jhlist6' },
            { name: '♥2022 속초 (4월)♥', link: '/hoonlog/jhlist7' },
            { name: '♥2022 부여 (4월)♥', link: '/hoonlog/jhlist8' },
            { name: '♥2022 2분기♥', link: '/hoonlog/jhlist9' },
            { name: '♥2022 북서울꿈의숲 (6월)♥', link: '/hoonlog/jhlist10' },
            { name: '♥2022 속초 (8월)♥', link: '/hoonlog/jhlist11' },
            { name: '♥2022 3분기♥', link: '/hoonlog/jhlist12' },
            { name: '♥2022 경주 (10월)♥', link: '/hoonlog/jhlist13' },
            { name: '♥2022 아쿠아리움 (12월)♥', link: '/hoonlog/jhlist14' },
