<style scoped>
body {
  background-color: #fff;
  color: #222;
  font-family: 'Helvetica Neue', sans-serif;
  margin: 0;
  padding: 0;
}

/* 네비게이션 */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 40px 60px;
  font-size: 16px;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.logo {
  font-weight: bold;
  font-size: 22px;
  text-decoration: none;
  color: black;
}

.nav-links a {
  margin-left: 30px;
  text-decoration: none;
  color: #555;
  transition: color 0.3s ease;
}
.nav-links a:hover {
  color: black;
}

/* 갤러리 */
.gallery {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
  padding: 40px 60px;
}

.gallery-item {
  position: relative;
  overflow: hidden;
  cursor: pointer;
}

.gallery-item img {
  width: 100%;
  height: auto;
  transition: transform 0.6s ease;
}

.gallery-item:hover img {
  transform: scale(1.05);
}

/* overlay (hover 시 연도 표시) */
.overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255,255,255,0.5);
  opacity: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: opacity 0.4s ease;
}

.gallery-item:hover .overlay {
  opacity: 1;
}

.overlay h2 {
  color: #000;
  font-weight: 400;
  letter-spacing: 2px;
}

/* 연도별 하위 리스트 */
.sublist {
  position: absolute;
  bottom: 0;
  left: 0;
  background-color: rgba(255,255,255,0.95);
  padding: 15px 25px;
  list-style: none;
  width: 100%;
  border-top: 1px solid #eee;
}

.sublist li {
  margin: 6px 0;
}

.sublist a {
  text-decoration: none;
  color: #333;
  transition: color 0.2s ease;
}

.sublist a:hover {
  color: #000;
}
</style>
